body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.search-bar {
  margin : 20px;
  text-align: center;
}

input {
  width : 85%;
  border:none;
  border-bottom: 1px solid gray;
  outline: none;
  text-indent: 5px;
}

input:focus {
  border-bottom: 1px solid red;
}

.video-item img {
  max-width: 65px;
}

.video-details iframe{
  border-radius: 4px;
}

.video-details .details{
  margin-top : 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.video-details .details .video-title{
  font-weight: bold;
  letter-spacing: 0.5px;
  font-size: 17px;
  padding-top: 10px;
}

.video-details .details .video-description{
  letter-spacing: 0.5px;
  font-size: 13px;
  padding-top: 10px;
}

.list-group-item{
  cursor: pointer;
}

.list-group-item:hover{
  background-color: #eee;
}

.list-group-item .media-heading {
  font-weight: 400;
  letter-spacing: 0.5px;
  font-size: 13px;
}



